<template>
  <div class="detail">
      {{bookName}}
  </div>
</template>
<script>
export default {
  data() {
    return {
      bookName: this.$route.params.id,
    };
  }
};
</script>
<style scoped>
.detail {
  width: 75%;
  float: right;
}
</style>



