<template>
  <div id="app">
    <h3>
      SPECIAL OFFER: 20% OFF BOOKS WITH WORDS IN THEM!!! LIMITED TIME ONLY!
    </h3>
    <hr />
    
    <div class="topbar">
      <img src="static/logo-horizontal.png" />
      <input type="text" name="fname" />
      <button>Search</button>
    </div>
    <hr />
    <router-view name="componentShop"></router-view>
    <router-view name="componentDetail"></router-view>
    <router-view name="componentSidebar"></router-view>
  </div>
</template>

<script>
import Shop from "./components/Shop";
import Sidebar from "./components/Sidebar";
import Detail from "./components/Detail";

export default {
  name: "App",
  components: {
    "component-sidebar": Sidebar,
    "component-Shop": Shop,
    "component-detail": Detail
  }
};
</script>

<style>
.topbar {
  display: block;
      position: relative;
    width: 100%;
    height: 40pt;
}
input {
  position: relative;
  top: -8pt;
  margin: 0 10pt;
  width: 250pt;

}
button {
  position: relative;
  top: -8pt;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 10px;
  margin-left: 10px;
}
h3 {
  text-align: center;
}
img {

  width: 180pt;
  height: 30pt;
}
</style>



